import { ColorSchemeName } from 'react-native';

export type ThemeColors = {
  background: {
    primary: string;
    secondary: string;
    tertiary: string;
  };
  text: {
    primary: string;
    secondary: string;
    tertiary: string;
    inverse: string;
  };
  border: string;
  primary: string;
  error: string;
  success: string;
  warning: string;
};

const lightColors: ThemeColors = {
  background: {
    primary: '#FFFFFF',
    secondary: '#F5F7FA',
    tertiary: '#E5E5E5',
  },
  text: {
    primary: '#1A1A1A',
    secondary: '#4A4A4A',
    tertiary: '#717171',
    inverse: '#FFFFFF',
  },
  border: 'rgba(0, 0, 0, .1)',
  primary: '#BF3475',
  error: '#FF3B30',
  success: '#34C759',
  warning: '#FF9500',
};

const darkColors: ThemeColors = {
  background: {
    primary: '#1A1A1A',
    secondary: '#2A2A2A',
    tertiary: '#3A3A3A',
  },
  text: {
    primary: '#FFFFFF',
    secondary: '#CCCCCC',
    tertiary: '#999999',
    inverse: '#1A1A1A',
  },
  border: '#404040',
  primary: '#FC8890',
  error: '#FF453A',
  success: '#32D74B',
  warning: '#FF9F0A',
};

export const getColors = (colorScheme: ColorSchemeName): ThemeColors => {
  return colorScheme === 'dark' ? darkColors : lightColors;
};

// For backward compatibility
export const colors = lightColors; 

