import React, { lazy, Suspense, useState, useEffect } from 'react';
import { View, Text, StyleSheet, Pressable, Platform, useWindowDimensions } from 'react-native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { useNavigation } from '@react-navigation/native';
import { RootStackParamList, RootStackNavigationProp } from '../types/navigation';
import { SearchBar } from '../components/SearchBar';
import { LoadingIndicator } from '../components/LoadingIndicator';
import { TableSettings } from '../components/TableSettings';
import { useTheme } from '../theme/ThemeContext';
import { useFoils } from '../hooks/useFoils';
import { useFilteredFoils } from '../hooks/useFilteredFoils';
import { FilterOptions } from '../types/filters';
import { CONTAINER_PADDING, MOBILE_BREAKPOINT, DEFAULT_COLUMNS, getTableWidth, getContentWidth, Column } from '../constants/layout';
import { loadColumns } from '../utils/storage';

// Lazy load screens
const BrowseScreen = lazy(() => import('../screens/BrowseScreen'));
const PrivacyPolicy = lazy(() => import('../screens/legal/PrivacyPolicy'));
const TermsOfService = lazy(() => import('../screens/legal/TermsOfService'));
const Contact = lazy(() => import('../screens/legal/Contact'));

const Stack = createNativeStackNavigator<RootStackParamList>();

interface AppNavigatorProps {
  searchQuery: string;
  onSearch: (text: string) => void;
}

const COLUMN_WIDTHS = {
  brand: 100,
  series: 120,
  model: 150,
  area: 80,
  wingspan: 80,
  aspectRatio: 80,
  type: 80,
  price: 105,
  link: 80,
} as const;

export const AppNavigator = ({ searchQuery, onSearch }: AppNavigatorProps) => {
  const navigation = useNavigation<RootStackNavigationProp>();
  const { colors } = useTheme();
  const { foils } = useFoils();
  const [isInitialized, setIsInitialized] = useState(false);
  const [columns, setColumns] = useState<Column[]>(DEFAULT_COLUMNS);
  const [filters, setFilters] = useState<FilterOptions>({
    brands: [],
    series: [],
    type: []
  });
  const { filterOptions } = useFilteredFoils(foils, filters);
  const { width } = useWindowDimensions();
  const isMobile = width < MOBILE_BREAKPOINT;

  // Load saved column settings on mount
  useEffect(() => {
    const loadSavedColumnSettings = async () => {
      try {
        const savedColumns = await loadColumns();
        if (savedColumns) {
          const updatedColumns = DEFAULT_COLUMNS.map(col => ({
            ...col,
            isVisible: typeof savedColumns[col.id] === 'boolean' ? savedColumns[col.id] : col.isVisible
          }));
          setColumns(updatedColumns);
        }
      } catch (error) {
        console.error('Error loading column settings:', error);
      } finally {
        setIsInitialized(true);
      }
    };

    loadSavedColumnSettings();
  }, []);

  const handleLogoPress = () => {
    navigation.reset({
      index: 0,
      routes: [{ name: 'Browse', params: { searchQuery } }],
    });
  };

  const handleFilterChange = (type: keyof FilterOptions, value: string) => {
    setFilters(prev => {
      const newFilters = { ...prev };
      if (type === 'type') {
        const array = prev.type as string[];
        newFilters.type = array.includes(value)
          ? array.filter(item => item !== value)
          : [...array, value];
      } else {
        const array = prev[type] as string[];
        newFilters[type] = array.includes(value)
          ? array.filter(item => item !== value)
          : [...array, value];
      }
      return newFilters;
    });
  };

  const handleColumnVisibilityChange = (columnId: string) => {
    setColumns(prev => {
      const newColumns = prev.map(col => 
        col.id === columnId ? { ...col, isVisible: !col.isVisible } : col
      );
      return newColumns;
    });
  };

  const handleClearFilters = () => {
    setFilters({
      brands: [],
      series: [],
      type: []
    });
  };

  const handleResetColumns = () => {
    setColumns(DEFAULT_COLUMNS);
  };

  // Calculate table width based on visible columns
  const tableWidth = React.useMemo(() => getTableWidth(columns), [columns]);

  const contentWidth = React.useMemo(() => 
    getContentWidth(tableWidth, width, isMobile), 
    [tableWidth, width, isMobile]
  );

  // Don't render the main content until column settings are loaded
  if (!isInitialized) {
    return <LoadingIndicator />;
  }

  return (
    <Stack.Navigator
      screenOptions={{
        headerStyle: {
          backgroundColor: colors.background.primary,
        },
        headerShadowVisible: false,
      }}
    >
      <Stack.Screen 
        name="Browse"
        options={{
          headerTitle: '',
          headerLeft: () => (
            <View style={[
              styles.headerOuterContainer,
              { backgroundColor: colors.background.primary },
              !isMobile && { alignItems: 'center' }
            ]}>
              {isMobile ? (
                // Mobile Layout
                <View style={styles.headerMainContainerMobile}>
                  <Pressable 
                    onPress={handleLogoPress}
                    style={({ pressed }) => [
                      styles.logoContainer,
                      pressed && styles.logoPressed
                    ]}
                  >
                    <Text style={[styles.headerTitle, { color: colors.primary }]}>FoilQuiver</Text>
                  </Pressable>
                </View>
              ) : (
                // Web Layout
                <View style={[
                  styles.headerMainContainerWeb,
                  { width: contentWidth }
                ]}>
                  <View style={styles.headerContentWeb}>
                    <Pressable 
                      onPress={handleLogoPress}
                      style={({ pressed }) => [
                        styles.logoContainer,
                        pressed && styles.logoPressed
                      ]}
                    >
                      <Text style={[styles.headerTitle, { color: colors.primary }]}>FoilQuiver</Text>
                    </Pressable>
                    <View style={styles.searchBarContainerWeb}>
                      <SearchBar 
                        value={searchQuery}
                        onChangeText={onSearch}
                        onClear={() => onSearch('')}
                      />
                    </View>
                    <TableSettings
                      filterOptions={filterOptions}
                      selectedBrands={filters.brands}
                      selectedSeries={filters.series}
                      selectedTypes={filters.type}
                      onFilterChange={handleFilterChange}
                      onClearFilters={handleClearFilters}
                      columns={columns}
                      onColumnVisibilityChange={handleColumnVisibilityChange}
                      onResetColumns={handleResetColumns}
                      foils={foils}
                    />
                  </View>
                </View>
              )}
            </View>
          ),
          headerRight: () => isMobile ? (
            <View style={styles.headerRightContainerMobile}>
              <View style={styles.searchBarContainerMobile}>
                <SearchBar 
                  value={searchQuery}
                  onChangeText={onSearch}
                  onClear={() => onSearch('')}
                />
              </View>
              <TableSettings
                filterOptions={filterOptions}
                selectedBrands={filters.brands}
                selectedSeries={filters.series}
                selectedTypes={filters.type}
                onFilterChange={handleFilterChange}
                onClearFilters={handleClearFilters}
                columns={columns}
                onColumnVisibilityChange={handleColumnVisibilityChange}
                onResetColumns={handleResetColumns}
                foils={foils}
              />
            </View>
          ) : null,
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <BrowseScreen 
              {...props} 
              filters={filters}
              onFilterChange={handleFilterChange}
              columns={columns}
            />
          </Suspense>
        )}
      </Stack.Screen>
      <Stack.Screen 
        name="Terms"
        options={{
          title: 'Terms of Service',
          headerStyle: {
            backgroundColor: colors.background.primary,
          },
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <TermsOfService {...props} />
          </Suspense>
        )}
      </Stack.Screen>
      <Stack.Screen 
        name="Privacy"
        options={{
          title: 'Privacy Policy',
          headerStyle: {
            backgroundColor: colors.background.primary,
          },
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <PrivacyPolicy {...props} />
          </Suspense>
        )}
      </Stack.Screen>
      <Stack.Screen 
        name="Contact"
        options={{
          title: 'Contact Us',
          headerStyle: {
            backgroundColor: colors.background.primary,
          },
        }}
      >
        {(props) => (
          <Suspense fallback={<LoadingIndicator />}>
            <Contact {...props} />
          </Suspense>
        )}
      </Stack.Screen>
    </Stack.Navigator>
  );
};

const styles = StyleSheet.create({
  headerOuterContainer: {
    flex: 1,
    paddingHorizontal: CONTAINER_PADDING,
  },
  // Mobile Styles
  headerMainContainerMobile: {
    flexDirection: 'row',
    alignItems: 'center',
    height: 56,
    width: '100%',
  },
  headerRightContainerMobile: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: 8,
    paddingRight: 12,
  },
  searchBarContainerMobile: {
    width: 160,
    height: 36,
  },
  // Web Styles
  headerMainContainerWeb: {
    height: 72,
    width: '100%',
  },
  headerContentWeb: {
    flexDirection: 'row',
    alignItems: 'center',
    gap: 24,
    height: '100%',
    width: '100%',
  },
  searchBarContainerWeb: {
    flex: 1,
    height: 40,
  },
  // Common Styles
  logoContainer: {
    height: '100%',
    justifyContent: 'center',
    minWidth: 120,
  },
  logoPressed: {
    opacity: 0.7,
  },
  headerTitle: {
    fontSize: 24,
    fontWeight: 'bold',
  },
}); 